* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.body {
    font-family: 'montseratt', sans-serif;
}

.app {
    background-image: url('./assets/coldimg.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease-out;
    min-height: 100vh;
}

main {
    min-height: 100vh;
    /* background-image: url('./assets/coldimg.jpg'); */
    /* background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75)); */
    padding: 25px;
}

.warm {
    background-image: url('./assets/warmimg.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.search-box {
    width: 100%;
    margin: 0 0 75px;
}

.search-box .search-bar {
    display: block;
    width: 100%;
    padding: 15px;
    appearance: none;
    background: none;
    border: none;
    outline: none;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 16px;
    margin-top: -25px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    color: #313131;
    font-size: 20px;
    transition: 0.4 ease;
}

.search-box .search-bar:focus {
    background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
    color: #fff;
    font-size: 37px;
    font-weight: 500px;
    text-align: center;
    /* text-shadow: 3px 3px rgba(50, 50, 70, 0.5); */
    color: black;
}

.location-box .date {
    color: #fff;
    font-size: 25px;
    font-weight: 300px;
    font-style: italic;
    text-align: center;
    /* text-shadow: 2px 2px rgba(50, 50, 70, 0.5); */
    margin-top: 20px;
    color: black;
}

.weather-box {
    text-align: center;
}

.weather-box .temp {
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    color: #fff;
    font-size: 102px;
    font-weight: 900;
    /* text-shadow: 3px 6px rgba(50, 50, 70, 0.5); */
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
    color: black;
}

.weather-box .weather {
    color: #fff;
    font-size: 48px;
    font-weight: 700px;
    /* text-shadow: 3px 3px rgba(50, 50, 70, 0.5); */
    color: black;
}

.search-box {
    margin-top: 30px;
}